<template>
  <div id="table">
    <el-row>
      <el-col :span="6" v-for="patient in tableData" :offset="1" >
        <el-card class="box-card" shadow="hover">
          <div class='over_name'>
            {{patient.name}}
          </div>
          <div :id="patient.patient_id" class="chart" @click="toDetail(patient)">
          </div>
          <div class="content">
            <div class="over_entry">
              Entries: {{patient.entry}}
            </div>
            <div class="over_updated">
              Updated: {{patient.day | dateformat}}
            </div>
            <div class='over_alert' v-if="patient.alert > 0">
              {{patient.alert}} Alert
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>  
  </div>
</template>

<script>
  import axios from 'axios'
  const storage= {}
  export default {
    data() {
      return {
        tableData: [],
        raw_data: null
      }
    },
    beforeRouteEnter(to, from, next) {
      const user = sessionStorage.user
      const get_path ='http://35.197.160.94:8094/doctor/overview?uid_doctor=' + user
      axios.get(get_path)
      .then(response=>{
         storage.list = response.data;
         next();
      })
      .catch(error=>{
          console.log(error);
      })
    },
    created() {
      this.raw_data = storage.list
      this.raw_data.forEach(el => {
          const _name = el.firstName+' '+el.lastName
          if (el.time != null ) {
            const patient_col = this.tableData.find(e => e.name === _name)
            if (patient_col) {
              switch(el.mood) {
                case 5:
                  patient_col.verygood_count ++;
                  break;
                case 4:
                  patient_col.good_count ++;
                  break;
                case 3:
                  patient_col.neutral_count ++;
                  break;
                case 2:
                  patient_col.bad_count ++;
                  break;
                case 1:
                  patient_col.verybad_count ++;
                  break;
              }
              patient_col.entry ++;
              const day = el.time.split('T')[0]
              if (day > patient_col.day) {
                patient_col.day = day
              }
            } else {
              let new_col = {
                name: _name,
                entry: 1,
                day: el.time.split('T')[0],
                alert: 0,
                supergood_count: 0,
                verygood_count: 0,
                good_count: 0,
                neutral_count: 0,
                bad_count: 0,
                verybad_count: 0,
                superbad_count: 0,
                patient_id: el.uid,
              }
              switch(el.mood) {
                case 6:
                  new_col.supergood_count ++;
                  break;
                case 5:
                  new_col.verygood_count ++;
                  break;
                case 4:
                  new_col.good_count ++;
                  break;
                case 3:
                  new_col.neutral_count ++;
                  break;
                case 2:
                  new_col.bad_count ++;
                  break;
                case 1:
                  new_col.verybad_count ++;
                  break;
                case 0:
                  new_col.superbad_count ++;
                  break;
              }
              this.tableData.push(new_col)
            }
          }
        })
    },
    mounted() {
      this.drawchart(this.tableData)
    },
    filters: {
      dateformat: function (value) {
        if (!value) return ''
        const date = new Date(value)
        const day = value.split('-')[2]
        const year = value.split('-')[0]
        const mounth = date.toDateString().split(" ")[1]
        const format = day + ' ' + mounth + ' ' + year
        return format
      },
    },
    methods: {
      formatter(row, column) {
        return row.address;
      },
      toDetail(e) {
        this.$store.dispatch('setPatient_idData', e.patient_id)
        this.$store.dispatch('setPatient_nameData', e.name)
        this.$router.push({path: '/Dashboard/Detail/trends'})
      },
      drawchart(raw) {
        console.log(raw)
        var avatar = '/icon_avatar.37c3ea6b.png'
        raw.forEach(el => {
          let overChart = this.$echarts.init(document.getElementById(el.patient_id))
          overChart.setOption({
            tooltip:{
              show: false,
            },
            grid: {
              top: 0
            },
            graphic: {
              elements: [{
                type: 'image',
                style: {
                  image: avatar,
                  width: 140,
                  height: 140
                },
                left: 'center',
                top: 'middle'
              }]
            },
            series: [{
              type: 'pie',
              radius: ['56%','70%'],
              color: ['#FEA200', '#ACDB10', '#3BDBB5', '#D5BB8F', '#ACADB6', '#686A7F', '#35364B'],
              labelLine: {
                normal: {
                  show: false
                }
              },
              data: [{
                value: el.supergood_count,
              }, {
                value: el.verygood_count,
              }, {
                value: el.good_count,
              }, {
                value: el.neutral_count,
              }, {
                value: el.bad_count,
              }, {
                value: el.verybad_count,
              }, {
                value: el.superbad_count,
              }]
            }]
          })
        })
      }
    },

  }
</script>

<style>
  #table .box-card {
    width: 100%;
    margin-top: 12px;
  }
  .chart {
  height: 240px;
  }
  .over_name {
    font-size: 18px;
    font-weight: bold;
    text-align: center;
  }
#table {
}
</style>